import React, { useState } from "react";
import styled from "styled-components";
import NavBar from "./components/navBar";
import Search from "./components/search";
import Movies from "./components/movies";

export default function App() {
  const [movies, setMovies] = useState([]);
  const [nominees, setNominees] = useState([]);
  const [errorMessage, setErrorMessage] = useState(null);
  const nominated = (movie) =>
    nominees.filter((nominee) => {
      return nominee.imdbID === movie.imdbID;
    }).length === 0;
  const addNominees = (movie) => {
    console.log(nominees);
    if (nominated(movie) && nominees.length < 5) {
      setNominees([...nominees, movie]);
    }
    return;
  };
  const remNominee = (nominee) => {
    let newNominees = nominees.filter(
      (movie) => movie.imdbID !== nominee.imdbID
    );
    setNominees(newNominees);
  };

  const theme = {
    green: "#008060",
    cream: "#FFFBBD",
    fawn: "#E6AA68",
    chocolate: "#1d1a05",
    red: "#ca3c25",
  };
  return (
    <AppStyle>
      <NavBar theme={theme} />
      <Search
        theme={theme}
        setMovies={setMovies}
        nominees={nominees}
        remNominee={remNominee}
        setErrorMessage={setErrorMessage}
      />
      <Movies
        theme={theme}
        movies={movies}
        nominees={nominees}
        addNominees={addNominees}
        errorMessage={errorMessage}
      />
    </AppStyle>
  );
}

const AppStyle = styled.div`
  body {
    font-family: "Raleway", "Helvetica Neue", Arial, sans-serif;
    font-size: 1em;
    line-height: 1.5;
    font-weight: 400;
    box-sizing: border-box;
  }
`;
