import React from "react";
import styled from "styled-components";

export default function MovieCard({
  theme,
  movie,
  loading,
  nominees,
  addNominees,
}) {
  const PLACEHOLDER = "https://i.imgur.com/3ywFyey.jpg";
  const nominated = nominees.some((m) => m.imdbID === movie.imdbID);
  return (
    <MovieCardStyles theme={theme}>
      <div className="movie-card">
        <img
          className="movie-img"
          // src={handleImage}
          src={!loading && movie.Poster !== "N/A" ? movie.Poster : PLACEHOLDER}
          alt={"Poster of the movie " + movie.Title}
        />
        <div className="text-overlay">
          <h2>{movie.Title}</h2>
          <p>({movie.Year})</p>
          <button
            className="nominate"
            disabled={nominated}
            onClick={() => addNominees(movie)}
          >
            {nominated ? "Nominated" : "Nominate"}
          </button>
        </div>
      </div>
    </MovieCardStyles>
  );
}

//? adapted from Brett Stevenson - https://codepen.io/liamj/pen/vYYLGZj?editors=1100
const MovieCardStyles = styled.div`
  font-family: "Raleway", "Helvetica Neue", Arial, sans-serif;

  .movie-card {
    display: block;
    position: relative;
    top: 7vw;
    width: 250px;
    height: 280px;
    margin: 1em;
    box-shadow: 0 2px 5px ${(props) => props.theme.chocolate};
    transition: all 450ms ease-out 0s;
    overflow: hidden;
  }
  .movie-card .movie-img {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    height: 100%;
    top: -9999px;
    left: -9999px;
    right: -9999px;
    bottom: -9999px;
    margin: auto;
    overflow: hidden;
  }
  .movie-card:hover {
    box-shadow: 0 4px 11px ${(props) => props.theme.chocolate};
  }
  .movie-card:hover .text-overlay {
    background: rgba(255, 255, 255, 0.9);
    height: 40%;
    top: 60%;
    transition: all 450ms ease-in-out 0s;
  }
  .movie-card:hover p {
    transition: all 350ms ease-in-out 0s;
  }
  .movie-card:hover p a {
    visibility: visible;
  }

  .text-overlay {
    position: relative;
    background: rgba(255, 255, 255, 0.75);
    width: 100%;
    height: 40%;
    top: 60%;
    box-shadow: 0 -2px 12px rgba(0, 0, 0, 0.3);
    padding: 10px 12px;
    overflow: hidden;
    transition: all 450ms ease-in-out 0s;
    cursor: pointer;
  }
  .text-overlay h2 {
    color: ${(props) => props.theme.chocolate};
    letter-spacing: 0.0225em;
    width: 95%;
    margin: 0;
    font-size: 1em;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .text-overlay p {
    color: ${(props) => props.theme.chocolate};
    width: 92%;
    margin: 8px 0;
    line-height: 1.25;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .nominate {
    position: absolute;
    z-index: 1;
    border: 0;
    outline: none;
    cursor: pointer;
    background: ${(props) => props.theme.green};
    transition: 0.5s;
    right: 2.3em;
    bottom: 3.5em;
    height: 2em;
    color: ${(props) => props.theme.cream};
  }
  button:disabled,
  button[disabled] {
    background: #999999;
  }
`;
