import React, { useState } from "react";
import styled from "styled-components";
import Nominees from "./nominees";

export default function Search({ theme, nominees, remNominee, setMovies, setErrorMessage }) {
  const MOVIE_API_URL = `https://shoppies-backend.herokuapp.com/api/search?q=`;
  const [searchValue, setSearchValue] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSearchValue = (e) => setSearchValue(e.target.value);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setErrorMessage(null);
    fetch(MOVIE_API_URL + searchValue)
      .then((res) => res.json())
      .then((jsonResponse) => {
        setLoading(false);
        if (jsonResponse.data.Error) {
          if (jsonResponse.data.Error === "Too many results.") {
            console.log(jsonResponse.data.Error);
            setErrorMessage("Too many results. Please be more specific üôè");
          } else if (jsonResponse.data.Error === "Movie not found!") {
            setErrorMessage("Hmmn.. Didn't find that movie üòü");
          } else {
            setErrorMessage(jsonResponse.data.Error);
          }
        } else {
          setMovies(jsonResponse.data.Search);
        }
      })
      .catch(function () {
        setErrorMessage("Seems you're offline :(");
        setLoading(false);
      });
  };

  return (
    <SearchStyle theme={theme} loading={loading ? 1:0}>
      <h1>Nominate your <span >5</span> Favourite movies for the Shoppies 2021!</h1>
      <Nominees nominees={nominees} remNominee={remNominee} theme={theme}/>
      <form onSubmit={handleSubmit} role="search">
        <input
          id="search"
          type="search"
          placeholder="Search for a movie..."
          autoFocus
          required
          value={searchValue}
          onChange={handleSearchValue}
        />
        <button type="submit">{loading ? "Searching..." : "Search"}</button>
      </form>
    </SearchStyle>
  );
}

//? adapted from Liam - https://codepen.io/liamj/pen/vYYLGZj?editors=1100
const SearchStyle = styled.div`
  font-family: "Kaushan Script", cursive;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    color: ${(props) => props.theme.chocolate};
    span {
      font-size: calc(3em - 40px);
      color: ${(props) => props.theme.red};
    }
  }

  form {
    position: relative;
    width: 30rem;
    background: ${(props) => props.theme.chocolate};
    border-radius: 0.7rem;
  }
  input,
  button {
    height: 5rem;
    font-family: "Raleway", "Helvetica Neue", Arial, sans-serif;
    border: 0;
    color: ${(props) => props.theme.fawn};
    font-size: 1.8rem;
  }

  input[type="search"] {
    outline: 0;
    width: 100%;
    background: #fff;
    padding: 0 1.6rem;
    border-radius: 0.7rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: all 0.3s cubic-bezier(0, 0, 0.43, 1.49);
    transition-property: width, border-radius;
    z-index: 1;
    position: relative;
    border: 2px solid ${(props) => props.theme.chocolate};
  }

  button {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    width: ${(props) => (props.loading ? "11rem" : "8rem")};
    font-weight: bold;
    background: ${(props) => props.theme.chocolate};
    border-radius: 0 0.7rem 0.7rem 0;
    cursor: pointer;
  }

  input:not(:-moz-placeholder-shown) {
    border-radius: 0.7rem 0 0 0.7rem;
    width: ${(props) =>
      props.loading ? "calc(100% - 11rem)" : "calc(100% - 8rem)"};
  }

  input:not(:-ms-input-placeholder) {
    border-radius: 0.7rem 0 0 0.7rem;
    width: ${(props) =>
      props.loading ? "calc(100% - 11rem)" : "calc(100% - 8rem)"};
  }

  input:not(:placeholder-shown) {
    border-radius: 0.7rem 0 0 0.7rem;
    width: ${(props) =>
      props.loading ? "calc(100% - 11rem)" : "calc(100% - 8rem)"};
  }
  input:not(:-moz-placeholder-shown) + button {
    display: block;
  }
  input:not(:-ms-input-placeholder) + button {
    display: block;
  }
  input:not(:placeholder-shown) + button {
    display: block;
  }
`;
