import React from "react";
import MovieCard from "./movieCard";
import styled from "styled-components";

export default function Movies({ theme, movies, loading, errorMessage, nominees, addNominees }) {
  return (
    <MovieStyles theme={theme}>
      {!errorMessage ? (
        movies.map((movie, key) => (
          <MovieCard
            key={key}
            movie={movie}
            theme={theme}
            loading={loading}
            nominees={nominees}
            addNominees={addNominees}
          />
        ))
      ) : (
        <p className="error">{errorMessage}</p>
      )}
    </MovieStyles>
  );
}

const MovieStyles = styled.div`
  display: flex;
  width: 100%;
  flex-flow: wrap;
  justify-content: center;

  .error {
    font-family: "Raleway", "Helvetica Neue", Arial, sans-serif;
    color: ${(props) => props.theme.red};
    font-size: 1.9rem;
  }
`;
