import React from "react";
import styled from "styled-components";

export default function Nominees({ theme, nominees, remNominee }) {
  return (
    <NomineesStyle theme={theme}>
      {nominees.length > 0 && (
        <div className={nominees.length > 4 && "full"}>
          <h2>Your nominees</h2>
          {nominees.length > 4 && (
              <p class="banner submit">Your Nominees are complete!</p>
          )}
          <ul>
            {nominees.map((movie, key) => {
              return (
                <li className="movie" key={key}>
                  {`${movie.Title} (${movie.Year})`}{" "}
                  <span
                    className="btn remove"
                    onClick={() => remNominee(movie)}
                  >
                    Remove
                  </span>
                </li>
              );
            })}
          </ul>
        </div>
      )}
    </NomineesStyle>
  );
}

const NomineesStyle = styled.div`
  .full {
    position: relative;
    background: ${(props) => props.theme.fawn};
    width: 28rem;
    padding: 7px;
  }
  .movie {
    list-style-type: disclosure-closed;
    margin: 0 1em 1em 0;
    font-family: "Raleway", "Helvetica Neue", Arial, sans-serif;
  }
  .banner {
    background: ${(props) => props.theme.red};
    padding: 2px 5px;
    color: ${(props) => props.theme.cream};
    cursor: pointer;
  }
  .remove {
  }
  .submit {
    position: absolute;
    top: 0;
    text-align: center;
    right: 0;
    padding: 0.5em 1.5em;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
  }
`;
